//this is where all the magic happens. below are the functions that manipulate the API and make it responsive 
import React, { Component } from 'react';
import './App.css';
import Table from './components/employeetable';
import SearchForm from './components/searchform';
import Header from './components/header';
import Api from './utils/api';


class App extends Component {
  state = {
    list: [],
    originalList: [],
    search: ""
  };

  componentDidMount() {
    Api.randomUser().then(response => {
      console.log(response.data.results)
      this.setState({
        list: response.data.results,
        originalList: response.data.results
      });
    });
  };

  handleInputChange = event => {
    const { name, value } = event.target
    this.setState({
      [name]: value
    })
    console.log(value);
    const newList = this.state.originalList.filter(employee => {
      return employee.name.first.toLowerCase().includes(value.toLowerCase()) || employee.name.last.toLowerCase().includes(value.toLowerCase())
    })
    this.setState({
      list: newList
    })
  };

  handleSubmit = event => {
    const newList = this.state.list.sort((a, b) => {
      return a.name.first.localeCompare(b.name.first)
    })
    this.setState({
      list: newList
    })
  };
  
  //this is where the components come together 
  render() {
    return (
      <div className="container">
        <Header />
        <SearchForm handleInputChange={this.handleInputChange} search={this.state.search} />
        <Table list={this.state.list} handleSubmit={this.handleSubmit} />

      </div>
    );

  };

};

export default App;
